import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import languageEN from "./locales/en/translation.json"; //   
import languageUA from "./locales/ua/translation.json"; //   
import languagePL from "./locales/pl/translation.json"; //   

i18n.use(initReactI18next).init({
  resources: {
    en: { translation: languageEN },
    ua: { translation: languageUA },
    pl: { translation: languagePL },
  },
  lng: "en", //   
  fallbackLng: "en", //  
  interpolation: {
    escapeValue: false, // React    
  },
  react: {
    useSuspense: false, // ,      
  },
});